	methods: {
		//点击一级分类切换
		tabbar(index){
			this.tabIndex = index;
			this.typefy = this.type[index]
			for (var typename in this.typedata) { //遍历对象属性名
				if (this.type[index] == typename) {
					this.typename = this.typedata[typename];
					console.log(this.typename);
				}
			}
		},
		//选择二级分类服务类型
		selecttype(e) {
			if (this.rSelect.indexOf(e) == -1) {
				console.log(e) //打印下标
				this.rSelect.push(e); //选中添加到数组里
				this.selectname.push(this.typename[e]);
			} else {
				this.rSelect.splice(this.rSelect.indexOf(e), 1); //取消
				this.selectname.splice(this.typename.indexOf(e), 1);
			}
		},
		//获取手机号码
		getPhoneNumber(e) {
			console.log("手机号返回信息");
			console.log(e);
			
			// // console.log(e.detail.errMsg);
			// console.log(e.detail.iv);
			// console.log(e.detail.encryptedData);
			

			// 	//-----------------是否授权，授权通过进入主页面，授权拒绝则停留在登陆界面
			if (e.detail.errMsg == "getPhoneNumber:fail user deny") { //用户点击拒绝
				uni.showToast({
					title: "拒绝获取手机号码"
				})
			} else { //允许授权执行跳转
				uni.showLoading({
					title: "获取中",
					duration:1500
				});
				if(this.isget){
					this.isget =false;
					let iv = e.detail.iv,
					encryptedData = e.detail.encryptedData,
					code = this.code,
					that = this;
					//检查session——key是否过期
					uni.checkSession({
						success(res) {
							console.log(res);
							that.$myRequest({
								url:'user/get_phone',
								data:{
									"code": code,
									"iv": iv,
									"encryptedData": encryptedData
								},
								methods:"GET"
								
							}).then(res=>{
								console.log(res);
							// const data = JSON.parse(res.data);
								if(res.data.code == 200){
									console.log(res.data.msg);
									that.phonenum = res.data.data;
									uni.hideLoading({
										title:"获取成功",
										duration:1500
									})
								}else if(res.data.code == 300){
									console.log(res.data.msg);
									// this.mescroll.endSuccess();
								}else{
									console.log(res.data.msg)
								}
							})
							
						},
						fail(err) {
							// session_key 已经失效，需要重新执行登录流程
							uni.login({
								success: res => {
									that.data.code = res.code
								}
							})
						}
					})
				}else{
					uni.showToast({
						title:"手机号码已存在"
					})
				}
			}
		},
		//选中协议
		checked() {
			return this.flag = !this.flag
		},
		//表单提交
		submit() {
			let name = true,
				phone = true,
				worktime = true,
				counttype = true;
			validator
				.init()
				.add(this.name, [{
						type: 'required',
						message: '用户名为空'
					},
					{
						type: 'min:2',
						message: '用户名小于2位!'
					},
					{
						type: 'max:8',
						message: '用户名大于8位!'
					}
				])
				.add(this.worktime, [{
						type: 'required',
						message: '手机号码为空'
					},
					{
						type: 'max:2',
						message: '工龄必须数字',
						callback :(value, message) =>  {
							var reg = '\d{2}';
						      return reg.test(value) ? void 0 : message 
						    }
					}
				])
				.add(this.phone, [{
						type: 'required',
						message: '手机号码为空'
					}
				])
				.add(this.counttype, [{
						type: 'required',
						message: '服务类型为空'
					},
					{
						type: 'min:6',
						message: '选择服务类型'
					},
				])
			const errorMsg = validator.validation()
			if (errorMsg) {
				uni.showToast({
					title:errorMsg
				});
				// console.error(errorMsg)
			} else if(!this.flag){
				uni.showToast({
					title:"请同意协议"
				});
			}
			else {
				uni.showLoading({
					title:"注册成功"
				})
				//获取首页中缓存的经纬度,uid
				let latitude = uni.getStorageSync("latitude");
				let longitude = uni.getStorageSync("longitude");
				let user_uid = uni.getStorageSync("uid");
				// console.log(user_uid);
				this.$myRequest({
					url:'user/get_info',
					data:{
						name: this.name,
						age: this.worktime,
						phone: this.phonenum,
						coord: latitude + "," + longitude,
						type: this.counttype,
						uid:user_uid
					},
					methods:"POST"
					
				}).then(res=>{
				// 	console.log(res);
				// const data = JSON.parse(res.data);
					if(res.data.code == 200){
						console.log(res.data.msg);
						//提交成功后的跳转到首页
						uni.hideLoading({
							title:"注册成功"
						});
						uni.setStorageSync("gonghao",res.id);
						setTimeout(()=>{
							uni.reLaunch({
								url:"../home/home"
							})
						},1500)
					}else if(res.data.code == 300){
						console.log(res.data.msg);
						
					}else{
						console.log(res.data.msg)
					}
				})
				// uni.request({
				// 	url: "https://applet.51tiaoyin.com/public/applet/user/get_info",
				// 	method: "POST",
				// 	data: {
				// 		name: this.name,
				// 		age: this.worktime,
				// 		phone: this.phonenum,
				// 		coord: latitude + "," + longitude,
				// 		type: this.counttype,
				// 		uid:user_uid
				// 	},
				// 	success(res) {
				// 		console.log(res);
				// 		//提交成功后的跳转到首页
				// 		uni.hideLoading({
				// 			title:"注册成功"
				// 		});
				// 		uni.setStorageSync("gonghao",res.id);
				// 		setTimeout(()=>{
				// 			uni.reLaunch({
				// 				url:"../home/home"
				// 			})
				// 		},2500)
						
				// 	},
				// 	fail() {
				// 		console.log("失败：" + res);
				// 	}
				// })
			}			
		}
	},
